import React from "react";
import {connect} from "react-redux";
import {
   Button,
   CardHeader,
   CardBody,
   Card,
   Row,
   Col,
   Form,
   FormGroup,
   CustomInput,
   Label,
   Input,
} from "reactstrap";
import {Mail, Lock, Check} from "react-feather";
import {Link} from "react-router-dom";
class Register extends React.Component {
   constructor(props) {
      super(props);
      this.state = {
         counter: 0,
      };
      console.log("PROPSSS", this.state.counter);
   }

   render() {
      return (
         <Row className="m-0">
            <Col sm="12" className=" pt-4">
               <Card className="rounded-0 mb-0 px-2">
                  <CardHeader>Register</CardHeader>
                  <CardBody>
                     <Col sm="12">
                        <Form>
                           <Row>
                              <Col sm="12">
                                 <FormGroup>
                                    <Label>FirstName</Label>
                                    <Input
                                       type="text"
                                       placeholder="First Name"
                                       name="firstName"
                                       //onChange={this.handleInput}
                                       required
                                    />
                                 </FormGroup>
                              </Col>
                              <Col sm="12">
                                 <FormGroup className="form-label-group">
                                    <Label>Last Name</Label>
                                    <Input
                                       type="text"
                                       placeholder="Last Name"
                                       name="lastName"
                                       onChange={this.handleInput}
                                       required
                                    />
                                 </FormGroup>
                                 <FormGroup className="form-label-group">
                                    <Label>Email</Label>
                                    <Input
                                       type="email"
                                       placeholder="Email"
                                       name="email"
                                       onChange={this.handleInput}
                                       required
                                    />
                                 </FormGroup>
                                 <FormGroup className="form-label-group">
                                    <Label>Password</Label>
                                    <Input
                                       type="password"
                                       placeholder="Password"
                                       name="password"
                                       onChange={this.handleInput}
                                       required
                                    />
                                 </FormGroup>
                                 <FormGroup className="form-label-group">
                                    <Label>Avtar</Label>
                                    <CustomInput
                                       type="file"
                                       id="avtar"
                                       name="avtar"
                                       onChange={this.handleavtar}
                                    />
                                 </FormGroup>
                                 <FormGroup className="form-label-group">
                                    <Label>Address</Label>
                                    <Input
                                       type="textarea"
                                       placeholder="Address"
                                       name="address"
                                       onChange={this.handleInput}
                                    />
                                 </FormGroup>
                                 <FormGroup className="form-label-group">
                                    <Label>Phone No</Label>
                                    <Input
                                       type="Number"
                                       placeholder="Phone No."
                                       name="mobile"
                                       onChange={this.handleInput}
                                    />
                                 </FormGroup>

                                 <Col
                                    className="d-flex justify-content-end flex-wrap mt-2"
                                    sm="12"
                                 >
                                    <Button
                                       className="mr-1"
                                       color="primary"
                                       type="submit"
                                       //onClick={this.props.leadId}
                                    >
                                       Register
                                    </Button>

                                    <Button
                                       color="primary"
                                       outline
                                       //onClick={() => this.handleReset()}
                                       type="button"
                                    >
                                       Reset
                                    </Button>
                                 </Col>
                              </Col>
                           </Row>
                        </Form>
                     </Col>
                  </CardBody>
               </Card>
            </Col>
         </Row>
      );
   }
}
const mapStateToProps = state => {
   return {
      isSuccess: state.globalReducer.isSuccess,
   };
};
const mapDispatchtoProps = dispatch => {
   return {
      handleClick: () => dispatch({type: "SUCCESS"}),
   };
};
export default connect(mapStateToProps, mapDispatchtoProps)(Register);
